import * as W from "./widgets";
import * as A from "./attributes";
import * as React from "react";
import * as I from "fp-ts/lib/IO";
import * as RR from "./react/renderer";
import { pipe } from "fp-ts/lib/pipeable";

type Model = {
  name: string;
  surname: string;
  age: number;
};

function render(model: Model) {
  let spamTest: string[] = [];
  for (let i = 0; i < 1000; i++) {
    spamTest.push("" + i);
  }
  return (update: (newModel: Model) => I.IO<void>) =>
    W.container(
      pipe(
        W.container(
          W.text(W.tr`EtÃ :`),
          pipe(
            W.number(model.age, age => update({ ...model, age }), true),
            A.id("age")
          )
        ),
        A.id("eta")
      ),
      pipe(
        W.container(
          W.text(W.tr`Nome:`),
          pipe(
            W.string(model.name, name => update({ ...model, name }), true),
            A.id("name"),
            A.enabled(model.age <= 18)
          )
        ),
        A.id("name")
      ),
      pipe(
        W.container(
          W.text(W.tr`Cognome:`),
          pipe(
            W.string(
              model.surname,
              surname => update({ ...model, surname }),
              true
            ),
            A.id("surname")
          )
        ),
        A.id("surname")
      ),
      pipe(
        W.text(
          W.tr`Ciao ${model.name} ${
            model.surname
          }, complimenti per i tuoi ${model.age.toFixed(0)} anni!`
        ),
        A.id("sum")
      ),
      pipe(
        W.button(W.tr`Salva`, () => () => console.log(model)),
        A.id("button")
      ),
      ...spamTest.map((_, i) =>
        pipe(
          W.container(
            pipe(W.text(W.tr`Nome:`), A.id("lol")),
            pipe(
              W.string(model.name, name => update({ ...model, name }), true),
              A.id("name" + i)
            )
          ),
          A.id("div" + i)
        )
      )
    );
}

let appModel: Model = {
  name: "",
  surname: "",
  age: 0
};

export function App() {
  return (
    <RR.AppRunner
      getApp={() => {
        return render(appModel)(newValue => {
          console.log("STATE UPDATE", newValue);
          return () => {
            console.log("STATE UPDATED", newValue);
            appModel = newValue;
          };
        });
      }}
    />
  );
}
